import React, { Component } from 'react'

import './background.scss';


interface isProps {
    readonly image: string | string[];
    readonly type?: string;
    readonly interval?: number;
    readonly 'animate-duration'?: string | number;
}

interface isState {
    images: string[];
    currentIndex: number;
}



class Background extends Component<isProps, isState> {
    timerId?: number;
    defaultProps?: isProps;
    private static defaultProps = {
        image: '',
        interval: 7000,
        'animate-duration': '4s'
    }
    constructor(props: isProps) {
        super(props)
        this.state = {
            images: [],
            currentIndex: 0
        }
    }
    static getDerivedStateFromProps(props: isProps, state: isState) {
        if (props.type !== 'group') {
            return { images: state.images.includes(props.image as string) ? state.images : [...state.images, props.image] }
        }
        return state

    }
    componentDidMount() {
        if (this.props.type === 'group') {
            const change = (images: string[], currentIndex: number) => {
                const newCurrentIndex = currentIndex + 2 > this.props.image.length ? 0 : currentIndex + 1;
                const target = this.props.image[newCurrentIndex];
                this.setState({
                    images: images.includes(target) ? images : [...images, target],
                    currentIndex: newCurrentIndex
                })
            }

            // 动画淡入淡出
            change(this.state.images, -1)
            this.timerId = window.setInterval(() => {
                const { images, currentIndex } = this.state;
                change(images, currentIndex)
            }, this.props.interval)
        }
    }
    componentWillUnmount() {
        window.clearInterval(this.timerId)
    }
    render() {
        const { images, currentIndex } = this.state;
        let imagesDom;
        if (this.props.type === 'group') {
            imagesDom = images.map((e, i) => (
                <div key={i} className={['image', 'bgids', 'animate__animated', i == currentIndex ? 'animate__fadeIn' : 'animate__fadeOut'].join(' ')} style={{ backgroundImage: `url(${e})` }}></div>
            ))
        } else {
            imagesDom = images.map((e, i) => (
                <div key={i} className={['image', 'bgids', 'animate__animated', e == this.props.image ? 'animate__fadeIn' : 'animate__fadeOut'].join(' ')} style={{ backgroundImage: `url(${e})` }}></div>
            ))
        }


        return (
            <div className="component-background">
                <style>
                    {`.animate__animated.animate__fadeIn,.animate__animated.animate__fadeOut{ --animate-duration: ${this.props['animate-duration']};}`}
                </style>
                <div className="background">
                    {imagesDom}
                </div>
                {this.props.children}
            </div>
        )
    }
}


export default Background;