import React from 'react';
import Index from '../pages/index/index';
import Portfolio from '../pages/portfolio/index/index';
import Effect from '../pages/portfolio/effect/index';
import Affachment from '../pages/portfolio/effect/vision/attachment/index';
import Space from '../pages/space/index';
import SpaceMain from '../pages/space/main/index' 
import Memorial from '../pages/space/memorial';


const routes : route[] = [
    {
        path: '/',
        component: Index,
    },
    {
        path: '/space',
        component: Space,
        redirect: '/space/main',
        children: [
            {
                path: '/space/main',
                component: SpaceMain
            },
            {
                path: '/space/memorial',
                component: Memorial
            }
        ]
    },
    {
        path: '/portfolio',
        component: Portfolio,
        children: [
            {
                path: '/space/main',
                component: SpaceMain
            },
            {
                path: '/space/memorial',
                component: Memorial
            }
        ]
    },
    {
        path: '/effect',
        component: Effect
    },
    {
        path: '/attachment',
        component: Affachment
    }
];



export default routes;