import React, { Component } from 'react'

import * as THREE from 'three';

import { Drawer, Input, InputNumber, Select } from 'antd';

import { MeshBasicMaterialParameters } from 'three'

import { OrbitControls } from '../../../utils/three/OrbitControls'

import { Geometry } from './selectData'

import './index.scss';
import { valueType } from 'antd/lib/statistic/utils';

interface isState {
    [propName: string]: any;
    MeshBasicMaterial: MeshBasicMaterialParameters;
    // geometryParameters: Parameters;
}
interface isProps {

}


class Portfolio extends Component<isProps, isState> {
    containerRef: any;
    camera: any;
    scene: any;
    renderer: any;
    model: any;

    constructor(props: any) {
        super(props);
        this.camera = null;
        this.scene = null;
        this.renderer = null;
        this.model = null;
        this.containerRef = React.createRef();
        this.state = {
            drawerVisible: true,
            MeshBasicMaterial: {
                color: 0xff5500,
                wireframe: true
            },
            GeometryIndex: 1,
           
        }

    }
    init() {
        const { MeshBasicMaterial, GeometryIndex } = this.state;
        const container = this.containerRef.current;
        const Class = Geometry[GeometryIndex].class;
        this.camera = new THREE.PerspectiveCamera(30, container.offsetWidth / container.offsetHeight, 1, 10000);
        this.camera.position.set(0, 0, 600)
        this.scene = new THREE.Scene();
        this.renderer = new THREE.WebGLRenderer();
        this.renderer.setSize(container.offsetWidth, container.offsetHeight)
        container.appendChild(this.renderer.domElement)

        const geometry = new Class(...Object.values(Geometry[GeometryIndex].parameters));
        this.model = new THREE.Mesh(geometry, new THREE.MeshBasicMaterial(MeshBasicMaterial))
        this.scene.add(this.model)

        new OrbitControls(this.camera, this.renderer.domElement)

        this.animation()
    }
  
    animation() {
        // this.model.rotateY(0.01);
        this.render3D()
        window.requestAnimationFrame(this.animation.bind(this));
    }
    render3D() {
        this.renderer.render(this.scene, this.camera)
    }

    setGeometryParameters(key: string, event: any) {
        const { GeometryIndex } = this.state;
        const Class = Geometry[GeometryIndex].class;
        const parameters = Geometry[GeometryIndex].parameters;
        parameters[key] = event;
        console.log(...Object.values(parameters));
        this.model.geometry = new Class(...Object.values(parameters));
    }

    updateGeometry(index: number) {
        const Class = Geometry[index].class;
        console.log(Geometry, index, Class);
        const parameters = Geometry[index].parameters;
        this.model.geometry = new Class(...Object.values(parameters));

    }

    setColor = (event: { target: any }) => {
        this.model.material.color = new THREE.Color(Number('0x' + event.target.value))
    }
    changeGeometry(value: number) {
        this.setState({ GeometryIndex: value })
        this.updateGeometry(value)

    }
    componentDidMount() {
        this.init()
    }

    render() {
        const { drawerVisible, MeshBasicMaterial, geometryParameters, GeometryIndex } = this.state;
        const parameters = Geometry[GeometryIndex].parameters;
        console.log(parameters);

        const parametersOptions = Object.keys(Geometry[GeometryIndex].parameters).map((key, index) => {
            return (
                <div className="item" key={key}>
                    <span className="tag">{key}</span>
                    <InputNumber
                        min={0}
                        onPressEnter={this.setGeometryParameters.bind(this, key)}
                        onChange={this.setGeometryParameters.bind(this, key)}
                        defaultValue={parameters[key]} />
                </div>
            )
        })


        return (
            <section className="Portfolio">
                <div className="container" ref={this.containerRef}></div>
                <Drawer title="setting" mask={false} placement="right" onClose={() => { this.setState({ drawerVisible: false }) }} visible={drawerVisible}>
                    <div className="block">
                    </div>
                    <div className="block">
                        <h4 className="target">-- -- Geometry -- --</h4>
                        <span className="tag">type：</span>
                        <Select defaultValue={GeometryIndex} style={{ width: 150 }} onChange={this.changeGeometry.bind(this)}>
                            {Geometry.map((item, index) => {
                                return (<Select.Option key={item.label} value={index}>{item.label}</Select.Option>)
                            })}
                        </Select>
                        {parametersOptions}
                    </div>
                    <div className="block">
                        <h4 className="target">-- -- Material -- --</h4>
                        <div className="row color">
                            <span className="tag">color</span> <Input onBlur={this.setColor} onPressEnter={this.setColor} addonBefore="#" defaultValue={`${MeshBasicMaterial.color?.toString(16)}`} />
                        </div>
                    </div>
                </Drawer>
            </section>
        )
    }

}

export default Portfolio