import { readdirSync } from 'fs';
import React from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'
import Space from '../pages/space'

const CreatedRouter = function ({ routes }: { routes: route[] | undefined }) {
    return (
        <Switch>
            {routes && routes.map((e, i) => (
                <Route
                    key={i}
                    exact={!(e.children && e.children.length)}
                    path={e.path}
                    render={props => (
                        <>
                            {e.redirect && <Redirect from={e.path} to={e.redirect} />}
                            <e.component {...props} $route={e} />
                        </>
                    )}>
                </Route>

            ))}
        </Switch>
    )
}


export default CreatedRouter
