import React, { Component } from 'react'
import { Card } from 'antd';

import './index.scss';

import userInfo from './userData';

import starSign from './starSign';

class Main extends Component {
    render() {
        const birth = new Date(userInfo.birthday);
        const now = new Date();
        const dms = Date.now() - birth.getTime();
        const days = Math.floor( dms / 1000 / 60 / 60 / 24 );
        const years = new Date().getFullYear() - birth.getFullYear() + 1;
        const age = new Date(now.getFullYear(), birth.getMonth(), birth.getDate() ).getTime() < Date.now() ? years - 1 : years - 2;
        const star = starSign.find(e => {
            const month = birth.getMonth() + 1;
            const date = birth.getDate();
            return  month >= e.startDate[0] && month <= e.endDate[0] && (date >= e.startDate[1] || date <=e.endDate[1])
        })?.name;
        const zodiac = ['鼠', '牛', '虎', '兔', '龙', '蛇', '马', '羊', '猴', '鸡', '狗', '猪'][Math.abs((birth.getFullYear() - 4) % 12 )];
        return (
            <div className="space-main">
                <Card title="Default size card" extra={<a href="#">More</a>} style={{ width: 600 }}>
                    <p>您好，{ userInfo.surname + (userInfo.gender === 0 ? '先生' : '女士') }。</p>
                    <p>以该界的计时模式计算，自您来到该界已有<strong>{ days }</strong>天。</p>
                    <p>这已经是您来到该界的第<strong>{ years }</strong>个年头。</p>
                    <p>在该界，您的</p>
                    <p>法定年龄（周岁）为： <strong>{age}</strong>，</p>
                    <p>民计年龄（虚岁）为: <strong>{years}</strong>。</p>
                    <p>所属星座为：<strong>{ star }</strong></p>
                    <p>生肖是： <strong>{ zodiac }</strong>.</p>
                </Card>
                <Card size="small" title="Small size card" extra={<a href="#">More</a>} style={{ width: 300 }}>
                    <p>Card content</p>
                    <p>Card content</p>
                    <p>Card content</p>
                </Card>
            </div>
        )
    }
}

export default Main;