import React, { Component } from 'react';
import { Space, Menu, Drawer, Button, Radio, InputNumber } from 'antd';
import { AppstoreOutlined, MailOutlined, SettingOutlined } from '@ant-design/icons';

import './index.scss';


import CreatedRouter from '../../utils/index'

import imgs from '../../background.config'

const { SubMenu } = Menu;

interface Props {
    $route: route
}

enum Operable { main, content, topbar, sidebar };

enum BackgroundType { color, image }

enum colorType { gradual, solid }

interface State {
    drawerVisible: Boolean;
    backgroundConfigOptions: {
        value: Operable;
        configOptions: {
            [propName: string]: {
                type: any
            }
        };
    };

}

const BackgroundImageConfig = function ({ targetKey, $parent, setting }: { targetKey: Operable, $parent: any, setting: any }) {
    return (
        <>
            <h4>图片选择</h4>
            <ul className="imgs">
                {imgs.map((e, i) => {
                    const path = `url(${e.default})`;
                    return (
                        <li key={i} className={setting.backgroundImage === path ? 'active' : ''} onClick={() => $parent.changeBackgroundStyle(targetKey, 'backgroundImage', path)}>
                            <img src={e.default} alt="" />
                        </li>
                    )
                })}
            </ul>
            <h4>处理方式</h4>
            <Radio.Group value={setting.backgroundRepeat} onChange={e => $parent.changeBackgroundStyle(targetKey, 'backgroundRepeat', e.target.value)}>
                <Radio.Button value="repeat">平铺</Radio.Button>
                <Radio.Button value="no-repeat">不平铺</Radio.Button>
            </Radio.Group>

            <h4>适应模式</h4>
            <Radio.Group value={setting.backgroundSize} onChange={e => $parent.changeBackgroundStyle(targetKey, 'backgroundSize', e.target.value)}>
                <Radio.Button value="cover">cover</Radio.Button>
                <Radio.Button value="contain">contain</Radio.Button>
            </Radio.Group>
            <h4>背景位置</h4>
            <Space direction="vertical">
                <div>
                    <span>x：</span>
                    <InputNumber
                        size="small"
                        min={0}
                        max={100}
                        defaultValue={50}
                        addonAfter="%"
                        style={{ width: '120px' }}
                        onChange={$parent.inputNumberChange.bind($parent, targetKey, 'x')} />
                </div>
                <div>
                    <span>y：</span>
                    <InputNumber
                        size="small"
                        addonAfter="%"
                        defaultValue={50}
                        min={0}
                        max={100}
                        style={{ width: '120px' }}
                        onChange={$parent.inputNumberChange.bind($parent, targetKey, 'y')} />
                </div>
            </Space></>
    )
}

const BackgroundColorConfig = function ({ $parent, $target }: { $parent: any; $target: any; }) {
    return (
        <>
            <Radio.Group
                options={$target.options}
                onChange={e => { $target.value = e.target.value; $parent.setState({}) }}
                value={$target.value}
            />
        </>
    )

}

class BackgroundConfigClass {
    name: string;
    value: Operable;
    type: any;
    [propsName: string]: any;
    constructor(name: string, val: Operable, type: BackgroundType, colorConfig?: object | undefined, imageConfig?: object | undefined) {
        this.name = name;
        this.value = val;
        imageConfig && (this.imageConfig = imageConfig);
        colorConfig && (this.colorConfig = colorConfig);
        this.type = {
            options: [
                { label: '颜色', value: BackgroundType.color },
                { label: '图片', value: BackgroundType.image },
            ],
            value: type || BackgroundType.color
        };
        this[BackgroundType[0]] = {
            options: [
                { label: '纯色', value: colorType[0] },
                { label: '渐变', value: colorType[1] }
            ],
            value: colorType[0]
        },
            this[BackgroundType[1]] = {
                value: 0
            }
    }
}


class ImageConfig {
    public style: object;
    constructor(style: object) {
        this.style = style
    }
}

class PersonalSpace extends Component<Props, State> {
    state = {
        backgroundConfigOptions: {
            value: Operable.main,
            configOptions: {
                [Operable.main]: new BackgroundConfigClass(
                    '全局背景',
                    Operable.main,
                    BackgroundType.image,
                    { style: { ackgroundColor: 'transparent' } },
                    new ImageConfig({
                        backgroundImage: `url(${require('../../assets/images/5.jfif').default})`,
                        backgroundSize: 'cover',
                        backgroundRepeat: 'no-repeat',
                        backgroundPosition: '50% 50%',
                    })),
                    [Operable.content]: new BackgroundConfigClass(
                    '内容',
                    Operable.content,
                    BackgroundType.color,
                    { style: { ackgroundColor: 'transparent' } },
                    new ImageConfig({
                        backgroundImage: `url(${require('../../assets/images/5.jfif').default})`,
                        backgroundSize: 'cover',
                        backgroundRepeat: 'no-repeat',
                        backgroundPosition: '50% 50%',
                    })),
                    [Operable.sidebar]: new BackgroundConfigClass(
                    '侧边栏',
                    Operable.sidebar,
                    BackgroundType.color,
                    { style: { ackgroundColor: 'transparent' } },
                    new ImageConfig({
                        backgroundImage: `url(${require('../../assets/images/5.jfif').default})`,
                        backgroundSize: 'cover',
                        backgroundRepeat: 'no-repeat',
                        backgroundPosition: '50% 50%',
                    })),
                    [Operable.topbar]: new BackgroundConfigClass(
                    '顶部栏',
                    Operable.topbar,
                    BackgroundType.color,
                    { style: { ackgroundColor: 'transparent' } },
                    new ImageConfig({
                        backgroundImage: `url(${require('../../assets/images/5.jfif').default})`,
                        backgroundSize: 'cover',
                        backgroundRepeat: 'no-repeat',
                        backgroundPosition: '50% 50%',
                    })),
            }
        },
        drawerVisible: false
    };
    handleClick = (e: object): void => {
        console.log('click ', e);
    };

    inputNumberChange = (targetKey: Operable, key: string, value: string | number) => {
        const style = { ...this.state.backgroundConfigOptions.configOptions[targetKey].imageConfig.style };
        const position = style.backgroundPosition.split(' ');
        position[key === 'x' ? 0 : 1] = value + '%';
        style.backgroundPosition = position.join(' ');
        this.state.backgroundConfigOptions.configOptions[targetKey].imageConfig.style = style;
        this.setState({})
    }

    // changeBackgroundRange(key: Operable, value: any) {
    //     this.state.backgroundConfigOptions.configOptions[key].imageConfig.backgroundRange = value;
    //     this.setState({})
    // }

    changeBackgroundStyle(key: Operable, css: string, val: string) {
        const style = { ...this.state.backgroundConfigOptions.configOptions[key].imageConfig.style };
        style[css] = val;
        this.state.backgroundConfigOptions.configOptions[key].imageConfig.style = style;
        this.setState({})
    }

    render() {
        const { drawerVisible, backgroundConfigOptions } = this.state;
        const { configOptions } = backgroundConfigOptions;

        const setTargetOptions = Object.values(configOptions).map(e => { return { label: e.name, value: e.value } })

        const key = backgroundConfigOptions.value;

        const backgroundTypeConfig = configOptions[key].type;

        const backgroundStyle = [Operable.main, Operable.content, Operable.sidebar, Operable.topbar].map(e => {
            return configOptions[e][`${BackgroundType[configOptions[e].type.value]}Config`].style;
        })

        const backgroundTypeTarget = configOptions[key][BackgroundType[backgroundTypeConfig.value]];

        return (
            <div className="space" style={backgroundStyle[0]}>
                <Menu
                    onClick={this.handleClick}
                    style={{ width: 256, ...backgroundStyle[2] }}
                    defaultSelectedKeys={['1']}
                    defaultOpenKeys={['sub1']}
                    mode="inline"

                    className="column-menu"
                >
                    <SubMenu key="sub1" icon={<MailOutlined />} title="Navigation One">
                        <Menu.ItemGroup key="g1" title="Item 1">
                            <Menu.Item key="1">Option 1</Menu.Item>
                            <Menu.Item key="2">Option 2</Menu.Item>
                        </Menu.ItemGroup>
                        <Menu.ItemGroup key="g2" title="Item 2">
                            <Menu.Item key="3">Option 3</Menu.Item>
                            <Menu.Item key="4">Option 4</Menu.Item>
                        </Menu.ItemGroup>
                    </SubMenu>
                    <SubMenu key="sub2" icon={<AppstoreOutlined />} title="Navigation Two">
                        <Menu.Item key="5">Option 5</Menu.Item>
                        <Menu.Item key="6">Option 6</Menu.Item>
                        <SubMenu key="sub3" title="Submenu">
                            <Menu.Item key="7">Option 7</Menu.Item>
                            <Menu.Item key="8">Option 8</Menu.Item>
                        </SubMenu>
                    </SubMenu>
                    <SubMenu key="sub4" icon={<SettingOutlined />} title="Navigation Three">
                        <Menu.Item key="9">Option 9</Menu.Item>
                        <Menu.Item key="10">Option 10</Menu.Item>
                        <Menu.Item key="11">Option 11</Menu.Item>
                        <Menu.Item key="12">Option 12</Menu.Item>
                    </SubMenu>
                </Menu>
                <div className="main-right">
                    <div onClick={this.handleClick} className="row-menu" style={backgroundStyle[3]}>
                        <SettingOutlined onClick={() => { this.setState({ drawerVisible: true }) }} />
                    </div>
                    <div className="child-pages" style={backgroundStyle[1]}>
                        <CreatedRouter routes={this.props.$route.children} />
                    </div>
                </div>
                <Drawer title="setting" mask={false} placement="right" onClose={() => { this.setState({ drawerVisible: false }) }} visible={drawerVisible}>
                    <Radio.Group
                        options={setTargetOptions}
                        value={backgroundConfigOptions.value}
                        onChange={e => { this.state.backgroundConfigOptions.value = e.target.value; this.setState({ }) }}
                    />
                    <h4>背景类型</h4>
                    <Radio.Group
                        options={backgroundTypeConfig.options}
                        value={backgroundTypeConfig.value}
                        onChange={e => { backgroundTypeConfig.value = e.target.value; this.setState({}) }}
                        optionType="button"
                    />
                    <br />
                    <br />
                    {backgroundTypeConfig.value === BackgroundType.color && <BackgroundColorConfig $parent={this} $target={backgroundTypeTarget} /> || backgroundTypeConfig.value === BackgroundType.image && <BackgroundImageConfig targetKey={key} $parent={this} setting={backgroundStyle[key]} />}
                </Drawer>
            </div>

        );
    }
}
export default PersonalSpace;