import React from 'react';
import { Link } from 'react-router-dom';

import { Drawer, Button } from 'antd';

import Background from '../../components/BG';

import background_images_list from '../../background.config.js';

import style from "./index.module.scss";

// interface Props {

// }

interface ItemType {
    name: string;
    path: string;
}

interface State {
    list: ItemType[];
    openDrawer: boolean;
    backgroundImageUrl: string;
}



class Index extends React.Component<any, State> {
    constructor(props: any) {
        super(props)
        this.state = {
            list: [{
                name: 'space',
                path: '/space'
            }, {
                name: 'document',
                path: ''
            }, {
                name: 'game',
                path: ''
            }, {
                name: 'photo',
                path: ''
            }, {
                name: 'portfolio',
                path: '/portfolio'
            }, {
                name: 'unknow',
                path: ''
            }],
            openDrawer: false,
            backgroundImageUrl: background_images_list[0].default
        }
    }

    closeDrawer = (): void => {
        this.setState({
            openDrawer: false
        })
    }

    openDrawer = (): void => {
        this.setState({
            openDrawer: true
        })
    }

    changeBackgroundImageUrl = (index: number): void => {
        this.setState({
            backgroundImageUrl: background_images_list[index].default
        })
    }

    render() {
        const list = this.state.list.map((e, i) => (
            <li key={i}>
                <Link to={e.path}>
                    <i className="animate__animated animate__fadeInUp animate__delay-1s">{e.name}</i>
                </Link>
            </li>));
        const background_images_list_dom = background_images_list.map((e, i) => (
            <li key={i} onClick={this.changeBackgroundImageUrl.bind(this, i)}>
                <img src={e.default} alt="" />
            </li>
        ))

        return (
            <Background image={background_images_list.map(e => e.default)} type="group">
                <div className={[style.Index, 'bgids'].join(' ')} >
                    <header>
                        <ul>
                            <li className="iconfont icon-clothes-full" onClick={this.openDrawer} />
                        </ul>
                    </header>



                    <ul className={[style.list, 'animate__animated', 'animate__fadeInUp'].join(' ')}>
                        {list}
                    </ul>

                    <Drawer title="Background Images" mask={false} placement="right" onClose={this.closeDrawer} visible={this.state.openDrawer}>
                        <ul className={style['background-image-list']}>{background_images_list_dom}</ul>
                    </Drawer>
                </div>
            </Background>

        )
    }
}


export default Index;